/* eslint-disable no-nested-ternary */
import { memo, ReactNode, useCallback, useMemo, useState } from 'react';
import dynamic from 'next/dynamic';
import formatDistance from 'date-fns/formatDistance';
import { I_CommentScene } from '@containers/Home/types';
import { CommentSceneItemWrapper } from './styled';

const CommentBox = dynamic(() => import('./CommentBox'));
const PopoverMenu = dynamic(() => import('@components/PopoverMenu'));

export const LIST_MENU = [
  {
    type: 'edit',
    classIcon: 'fas fa-pen',
    label: 'Edit',
    isOwnerComment: true,
    isOwnerTour: false,
  },
  {
    type: 'resolved',
    classIcon: 'fas fa-check',
    label: 'Mark Resolve',
    isOwnerTour: true,
    isOwnerComment: false,
  },
  {
    type: 'processing',
    classIcon: 'fas fa-undo-alt',
    label: 'Mark Active',
    isOwnerTour: true,
    isOwnerComment: false,
  },
  {
    type: 'remove',
    classIcon: 'fas fa-trash-alt',
    label: 'Delete',
    isOwnerTour: true,
    isOwnerComment: true,
  },
];

interface Props {
  commentScene: I_CommentScene;
  layout?: 'children' | 'parent';
  children?: ReactNode;
  onClick?: (commentScene) => void;
  onUpdate: (commentScene) => void;
  onDelete: (commentScene) => void;
}

const CommentSceneItem = memo(
  ({
    layout = 'parent',
    children,
    commentScene,
    onClick,
    onUpdate,
    onDelete,
  }: Props) => {
    const listMenu = useMemo(
      () =>
        LIST_MENU.filter(item => {
          let isShow = false;
          if (item.isOwnerComment && item.isOwnerTour)
            isShow = commentScene?.isOwnerComment || commentScene?.isOwnerTour;
          else if (item.isOwnerComment && !item.isOwnerTour) {
            isShow = commentScene?.isOwnerComment;
          } else if (!item.isOwnerComment && item.isOwnerTour) {
            isShow =
              item.type === 'resolved'
                ? commentScene?.isOwnerTour &&
                  !commentScene.isResolved &&
                  !commentScene.parent
                : item.type === 'processing'
                ? commentScene?.isOwnerTour &&
                  commentScene.isResolved &&
                  !commentScene.parent
                : commentScene?.isOwnerTour;
          }
          return isShow;
        }),
      [commentScene],
    );

    const [isEdit, setEdit] = useState(false);

    const _onClick = useCallback(() => {
      if (onClick) onClick(commentScene);
    }, [onClick, commentScene]);

    const onTriggerAction = useCallback(
      ({ type }) => {
        switch (type) {
          case 'edit':
            setEdit(true);
            break;
          case 'resolved':
            onUpdate({
              ...commentScene,
              ath: undefined,
              atv: undefined,
              message: undefined,
              scene_id: commentScene.id,
              isResolved: true,
            });
            break;
          case 'processing':
            onUpdate({
              ...commentScene,
              ath: undefined,
              atv: undefined,
              message: undefined,
              scene_id: commentScene.id,
              isResolved: false,
            });
            break;
          case 'remove':
            onDelete(commentScene);
            break;
          default:
            break;
        }
      },
      [commentScene, onDelete, onUpdate],
    );

    const onCancelEditing = useCallback(() => setEdit(false), []);

    const onSubmitEditing = useCallback(
      value =>
        onUpdate({
          ...commentScene,
          message: value,
          ath: undefined,
          atv: undefined,
          isResolved: undefined,
          scene_id: commentScene.id,
        }),
      [commentScene, onUpdate],
    );

    return (
      <CommentSceneItemWrapper
        className={`${layout}-layout`}
        onClick={_onClick}
      >
        <div className="--main">
          {layout === 'parent' ? (
            <>
              <div className="user">
                <div className="avt">
                  <img src={commentScene.user.avatar || ''} alt="" />
                </div>
                <div className="info">
                  <div className="name">
                    {commentScene.user.name || commentScene.user.email || ''}
                  </div>
                  <div className="time">
                    {formatDistance(
                      new Date(commentScene.created_at),
                      new Date(),
                      {
                        addSuffix: false,
                      },
                    )}
                  </div>
                </div>
              </div>
              {isEdit ? (
                <CommentBox
                  initValue={commentScene.message}
                  onCancel={onCancelEditing}
                  onSubmit={onSubmitEditing}
                />
              ) : (
                <div className="txt">{commentScene.message}</div>
              )}
              {children}
            </>
          ) : (
            <>
              <div className="avt">
                <img src={commentScene.user.avatar || ''} alt="" />
              </div>
              <div className="content">
                <div className="info">
                  <div className="name">
                    {commentScene.user.name || commentScene.user.email || ''}
                  </div>
                  <div className="time">
                    {formatDistance(
                      new Date(commentScene.created_at),
                      new Date(),
                      {
                        addSuffix: false,
                      },
                    )}
                  </div>
                </div>
                {isEdit ? (
                  <CommentBox
                    initValue={commentScene.message}
                    onCancel={onCancelEditing}
                    onSubmit={onSubmitEditing}
                  />
                ) : (
                  <div className="txt">{commentScene.message}</div>
                )}
              </div>
              {children}
            </>
          )}
        </div>
        {!!listMenu.length && (
          <div className="--extra">
            <PopoverMenu
              listMenu={listMenu}
              onTriggerAction={onTriggerAction}
              placement="bottom"
            >
              <i aria-hidden className="fas fa-ellipsis-v" />
            </PopoverMenu>
          </div>
        )}
      </CommentSceneItemWrapper>
    );
  },
);

export default CommentSceneItem;
